.login_contents {
    width: 35%;
    background-color: white;
    margin: 0 auto;
    padding: 3rem;
    text-align: center;
    border-radius: 15px;
  }
  .loginMain {
    padding-top: 3%;
    background-image: url("https://c4.wallpaperflare.com/wallpaper/981/204/987/simple-background-wallpaper-preview.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
  }
  .hospitalLoginMain {
    padding-top: 3%;
    /* background-image: url("https://c4.wallpaperflare.com/wallpaper/981/204/987/simple-background-wallpaper-preview.jpg"); */
    background-image: url("https://static.vecteezy.com/system/resources/previews/003/127/950/original/abstract-soft-blue-background-with-dynamic-waves-shape-free-vector.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
  }
  .loginContainer {
    padding-top: 3%;
  }
  .input_login {
    width: 100%;
  }
  .login_button {
    background-color: rgb(23, 129, 174) !important;
    width: 100%;
    padding: 0.6rem !important;
    font-size: 1.2rem !important;
  }
  
  .loginIcons {
    color: rgb(23, 129, 174) !important;
  }
  @media screen and (max-width: 1920px) {
    .input_login,
    .login_button {
      width: 70%;
    }
    .loginMain {
      padding-top: 10%;
    }
    .login_contents {
      padding: 6rem 3rem;
    }
  }
  @media screen and (max-width: 1440px) {
    .input_login,
    .login_button {
      width: 90%;
    }
    .loginMain {
      padding-top: 4%;
    }
    .login_contents {
      padding: 3rem;
    }
  }
  @media screen and (max-width: 940px) {
    .login_contents {
      width: 60%;
    }
  }
  @media screen and (max-width: 580px) {
    .login_contents {
      width: 70%;
      padding: 1.5rem;
    }
    .loginContainer {
      padding-top: 3rem;
    }
  }
  @media screen and (max-width: 450px) {
    .login_contents {
      width: 77%;
    }
  }
  @media screen and (max-width: 450px) {
    .input_login,
    .login_button {
      width: 100%;
    }
    .loginMain {
      padding-bottom: 4rem;
    }
  }
  @media screen and (max-width: 360px) {
    .input_login {
      width: 13rem !important;
    }
    .login_button {
      width: 13rem !important;
    }
  }
  