.profile {
  padding: 4rem;

}

.profile_main {

  margin: 0.5rem;
  background-color: white;
  box-shadow: -9px 8px 56px -3px rgba(0,0,0,0.75);
}
.profile_banner {

  background: linear-gradient(
    110deg,
    rgb(247, 131, 150),
    pink,
    rgb(247, 131, 150),
    pink,
    rgb(247, 131, 150)
  );
  text-align: center !important;
  padding: 1rem;
}
.profile_banner img {
  border-radius: 50%;
  height: 14rem !important;
  width: 14rem;
}
li{
  list-style: none;
}
.contents li {
  list-style-type: none;
  line-height: 2.4rem;
}
.contents h2,
h3 {
  color: var(--secondaryColor);
  font-size: 1rem;
}
.contents {
  padding: 1.5rem;
}
.contact {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 4rem;
}
.contact p {
  justify-content: left;
}
.contents span {
  font-weight: 100;
  padding-left: 0.3rem;
}

@media screen and (max-width: 500px) {
  .profile_banner img {
    width: 80%;
  }
  .profile {
    padding: 1rem;
  }
  .Profile_list li {
    text-align: left;
  }
  .contents{
   margin-left: 15%;
  }
}
.tick{
  width: 1.2rem !important;
  color: rgba(6, 6, 74, 0.685);
  margin-top: 1rem;
}
.jobList{
  height: 20rem;
  overflow-y: scroll;
  padding-bottom: 10%;
}
.jobList::-webkit-scrollbar{
display: none;
}
.hospitalImg{
  width: 100%;
  height: 300px;
}
@media screen and (max-width: 1920px) {
  .hospitalImg{
    height: 400px;
  }
  .profile_banner img {
    height: 16rem !important;
    width: 16rem;
  }
}
@media screen and (max-width: 1600px) {
  .hospitalImg{
    height: 300px;
  }
  .profile_banner img {
    height: 12rem !important;
    width: 12rem;
  }
}